<template>
  <div class="bdthhd">
    <div>Tình hình hoạt động các trạm, điểm</div>
    <div class="chart">
      <canvas id="pieChart" width="250" height="250"></canvas>
    </div>
  </div>
</template>

<script>
import Chart from "chart.js";
export default {
  name: "BieuDoTinhHinhHoatDong",
  data() {
    return {
      listInfo: [
        {
          title: "Trong hạn xử lý",
          color: "rgb(119, 187, 246)",
          num1: "3,705",
          num2: "28.48%",
        },
        {
          title: "Trả KQ quá hạn",
          color: "rgb(211, 111, 117)",
          num1: "221",
          num2: "1.62%",
        },
        {
          title: "Trả KQ đúng hạn",
          color: "rgb(41, 108, 182)",
          num1: "8,946",
          num2: "68.77%",
        },
        {
          title: "Quá hạn xử lý",
          color: "rgb(235, 52, 57)",
          num1: "147",
          num2: "1.13%",
        },
      ],
    };
  },
  mounted() {
    let ctx = document.getElementById("pieChart").getContext("2d");
    new Chart(ctx, {
      type: "pie",
      data: {
        datasets: [
          {
            fill: true,
            backgroundColor: [
              "rgb(235, 52, 57)",
              "rgb(119, 187, 246)",
              "rgb(211, 111, 117)",
              "rgb(41, 108, 182)",
            ],
            data: [1.13, 28.48, 1.62, 68.77],
            borderWidth: 0,
          },
        ],
      },
      options: {
        rotation: 2 * Math.PI,
        tooltips: {
          enabled: false,
        },
        plugins: {
          datalabels: {
            formatter: (value, ctx) => {
              let sum = 0;
              if (ctx.dataset.data) {
                for (let i = 0; i < ctx.dataset.data.length; i++) {
                  sum += ctx.dataset.data[i];
                }
              }
              let percentage = (value * 100) / sum;
              return percentage.toFixed(2) + "%";
            },
            color: "#fff",
            font: {
              size: 10,
            },
          },
        },
      },
    });
  },
};
</script>
<style scoped lang="scss">
.bdthhd {
  .chart {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
</style>
